.wp-block-create-block-icon-list-item {
	height: auto !important;
	position: relative;
	min-height: 40px;
	display: flex; // Changed to flex for easier child alignment
	align-items: center;
	width: var(--items-width);

	// --- Base State ---
	background: var(--background-color, transparent);
	border-color: var(--border-color, transparent);

	/* 2. Individual Border Styles */
	border-style: var(--border-style, none);

	/* 3. Individual Border Widths */
	border-top-width: var(--border-top-width, initial);
	border-right-width: var(--border-right-width, initial);
	border-bottom-width: var(--border-bottom-width, initial);
	border-left-width: var(--border-left-width, initial);

	/* 4. Individual Border Radii */
	border-top-left-radius: var(--border-radius-top, initial);
	border-top-right-radius: var(--border-radius-right, initial);
	border-bottom-right-radius: var(--border-radius-bottom, initial);
	border-bottom-left-radius: var(--border-radius-left, initial);

	/* 5. Individual Padding */
	margin-top: var(--margin-top, initial);
	margin-right: var(--margin-right, initial);
	margin-bottom: var(--margin-bottom, initial);
	margin-left: var(--margin-left, initial);

	/* Padding */

	padding-top: var(--padding-top, initial);
	padding-right: var(--padding-right, initial);
	padding-bottom: var(--padding-bottom, initial);
	padding-left: var(--padding-left, initial);

	/* 7. Box Shadow */
	box-shadow: var(--box-shadow, none);

	transition: all 0.2s ease-in-out;

	// --- Unified Hover State ---
	&:hover {
		//line break

		/* 1. Background & Shadow */
		background: var(--bg-h, var(--background-color, transparent));
		box-shadow: var(--box-shadow-h, var(--box-shadow, none));

		/* 2. Border Style & Color */
		border-color: var(--border-color-h, var(--border-color, transparent));
		border-style: var(--border-style-h, var(--border-style, none));

		/* 3. Border Widths on Hover */
		border-top-width: var(--border-top-h, var(--border-top-width, initial));
		border-right-width: var(
			--border-right-h,
			var(--border-right-width, initial)
		);
		border-bottom-width: var(
			--border-bottom-h,
			var(--border-bottom-width, initial)
		);
		border-left-width: var(
			--border-left-h,
			var(--border-left-width, initial)
		);

		/* 4. Border Radius on Hover */
		border-top-left-radius: var(
			--radius-top-h,
			var(--border-radius-top, initial)
		);
		border-top-right-radius: var(
			--radius-right-h,
			var(--border-radius-right, initial)
		);
		border-bottom-right-radius: var(
			--radius-bottom-h,
			var(--border-radius-bottom, initial)
		);
		border-bottom-left-radius: var(
			--radius-left-h,
			var(--border-radius-left, initial)
		);

		/* 5. Padding on Hover (Optional, use if you want the pill to grow/shrink) */
		padding-top: var(--padding-top-h, var(--padding-top, initial));
		padding-right: var(--padding-right-h, var(--padding-right, initial));
		padding-bottom: var(--padding-bottom-h, var(--padding-bottom, initial));
		padding-left: var(--padding-left-h, var(--padding-left, initial));

		/* 6. Margin on Hover */
		margin-top: var(--margin-top-h, var(--margin-top, initial));
		margin-right: var(--margin-right-h, var(--margin-right, initial));
		margin-bottom: var(--margin-bottom-h, var(--margin-bottom, initial));
		margin-left: var(--margin-left-h, var(--margin-left, initial));
	}

	// --- CENTRALIZED GRANDCHILD LOGIC ---
	// This removes the need for hover/border logic in the other two files
	.wp-block-create-block-icon-picker,
	.wp-block-create-block-advanced-text {
		background: transparent !important; // Force transparency
		box-shadow: none !important;

		/* 4. Individual Border Radii */
		border-top-left-radius: var(--border-radius-top, initial);
		border-top-right-radius: var(--border-radius-right, initial);
		border-bottom-right-radius: var(--border-radius-bottom, initial);
		border-bottom-left-radius: var(--border-radius-left, initial);

		border-color: var(--child-border-color, transparent);
		border-style: var(--child-border-style, none);
		border-top-width: var(--child-border-top-width, 0);
		border-right-width: var(--child-border-right-width, 0);
		border-bottom-width: var(--child-border-bottom-width, 0);
		border-left-width: var(--child-border-left-width, 0);

		padding-top: var(--padding-top-h, var(--padding-top, initial));
		padding-right: var(--padding-right-h, var(--padding-right, initial));
		padding-bottom: var(--padding-bottom-h, var(--padding-bottom, initial));
		padding-left: var(--padding-left-h, var(--padding-left, initial));

		/* 6. Margin on Hover */
		margin-top: var(--margin-top-h, var(--margin-top, initial));
		margin-right: var(--margin-right-h, var(--margin-right, initial));
		margin-bottom: var(--margin-bottom-h, var(--margin-bottom, initial));
		margin-left: var(--margin-left-h, var(--margin-left, initial));

		&:hover {
			background: var(
				--bg-h,
				var(--background-color, transparent)
			) !important;
		}

		// Add radius and hover for children here if desired
	}
}
